.modal {
  display: none;
  /* display: block; */
  overflow-y: auto;
  padding: 30px 10px;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.9);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2000; }

.modal.show {
  display: block; }

.modal__inner {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100%; }

.modal__content {
  max-width: 400px;
  width: 100%;
  padding: 25px;
  position: relative;
  background-color: #fff;
  opacity: 0;
  transform: scale(0.1);
  transition: transform 0.2s ease-out, opacity 0.2s ease-out; }

.modal__header {
  margin-bottom: 25px;
  text-align: center; }

.modal__icon {
  margin-bottom: 10px; }

.modal__title {
  margin-bottom: 10px;
  font-family: "Lato", sans-serif;
  font-size: 24px;
  color: #004af2;
  font-weight: 900;
  line-height: 1.2; }

.modal__text {
  font-size: 16px;
  color: #2c2c2c; }

.modal__footer {
  margin-top: 25px;
  padding-top: 25px;
  border-top: 1px solid #e8e8e8;
  text-align: center; }

.modal__footer-title {
  font-family: "Lato", sans-serif;
  font-size: 18px;
  font-weight: 900;
  color: #2c2c2c;
  margin-bottom: 10px; }

.modal__contacts {
  font-size: 14px;
  color: #2c2c2c;
  font-style: normal; }

.modal__contacts p {
  margin-bottom: 3px; }

.modal__contacts a {
  color: inherit;
  text-decoration: none; }

.modal__contacts a:hover {
  text-decoration: underline; }

.modal__close {
  width: 20px;
  height: 20px;
  padding: 0;
  background: none;
  border: none;
  cursor: pointer;
  position: absolute;
  top: -20px;
  right: -30px;
  z-index: 1;
  transition: transform 0.3s linear; }

.modal__close:focus {
  outline: none; }

.modal__close:hover {
  transform: scale(1.2); }

.modal__close img {
  display: block; }

.modal__title--white,
.modal__text--white {
  color: #fff; }

.modal--thanks .modal__inner {
  display: flex;
  flex-direction: column; }

@media (max-width: 575px) {
  .modal__close {
    right: 0;
    top: -30px; } }

